        <tr v-colorize-nav="index" v-for="(item, index) in tableData" :key="index">
          <td>{{ item.date }}</td>
          <td>{{ item.name }}</td>
          <td>{{ item.address }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [
        { date: "2022-01-01", name: "张三", address: "北京" },
        { date: "2022-01-02", name: "李四", address: "上海" },
        { date: "2022-01-03", name: "王五", address: "广州" }
      ]
    };
  }
};
</script>

<style>
.styled-table {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  font-family: Arial, sans-serif;
}

.styled-table th {
  background-color: #f2f2f2;
  color: #333;
  font-weight: bold;
}

.styled-table th,
.styled-table td {
  border: 1px solid #ddd;
  padding: 10px;
}

.styled-table tbody tr:nth-child(odd) {
  background-color: #f9f9f9;
}

.styled-table tbody tr:hover {
  background-color: #f1f1f1;
}

.styled-table tbody tr:first-child {
