<template>
  <div id="box">
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item label="活动名称">
        <el-input v-model="form.name"></el-input>
      </el-form-item>
      <el-form-item label="活动区域">
        <el-select v-model="form.region" style="width: 100%;" placeholder="请选择活动区域">
          <el-option label="区域一" value="shanghai"></el-option>
          <el-option label="区域二" value="beijing"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="活动时间">
        <el-col :span="11">
          <el-date-picker
            type="date"
            placeholder="选择日期"
            @change="formatDate"
            v-model="form.date1"
            style="width: 100%;"
          ></el-date-picker>
        </el-col>
        <el-col class="line" :span="2">-</el-col>
        <el-col :span="11">
          <el-time-picker
            placeholder="选择时间"
            @change="formatDateTime"
            v-model="form.date2"
            style="width: 100%;"
          ></el-time-picker>
        </el-col>
      </el-form-item>
      <el-form-item label="即时配送">
        <el-switch v-model="form.delivery"></el-switch>
      </el-form-item>
      <el-form-item label="活动性质">
        <el-checkbox-group v-model="form.type">
          <el-checkbox label="美食/餐厅线上活动" name="type"></el-checkbox>
          <el-checkbox label="地推活动" name="type"></el-checkbox>
          <el-checkbox label="线下主题活动" name="type"></el-checkbox>
          <el-checkbox label="单纯品牌曝光" name="type"></el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="特殊资源">
        <el-radio-group v-model="form.resource">
          <el-radio label="线上品牌商赞助"></el-radio>
          <el-radio label="线下场地免费"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="活动形式">
        <el-input type="textarea" v-model="form.desc"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="drawerFun">立即创建</el-button>
        <el-button @click="qxbtn">取消</el-button>
      </el-form-item>
    </el-form>
    <!-- <el-button @click="drawer = true" type="primary" style="margin-left: 16px;">点我打开</el-button> -->
    <el-drawer title="活动" :visible.sync="drawer" :direction="direction" :before-close="handleClose">
      <p>
        活动名称:
        <span>{{names}}</span>
      </p>
      <p>
        活动区域:
        <span>{{regions}}</span>
      </p>
      <p>
        活动时间:
        <span>{{date1s+"-"+date2s}}</span>
      </p>
      <p>
        即时配送:
        <span>{{deliverys}}</span>
      </p>
      <p>
        活动性质:
        <span>{{types}}</span>
      </p>
      <p>
        特殊资源:
        <span>{{resources}}</span>
      </p>
      <p>
        活动形式:
        <span>{{descs}}</span>
      </p>
    </el-drawer>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: ""
      },
      drawer: false,
      direction: "rtl",
      names: "",
      regions: "",
      date1s: "",
      date2s: "",
      deliverys: false,
      types: [],
      resources: "",
      descs: ""
    };
  },
  methods: {
    drawerFun() {
      this.drawer = true;
      this.names = this.form.name;
      this.regions = this.form.region;
      this.date1s = this.form.date1;
      this.date2s = this.form.date2;
      this.deliverys = this.form.delivery;
      this.types = String(this.form.type);
      this.resources = this.form.resource;
      this.descs = this.form.desc;
      this.form.name = "";
      this.form.region = "";
      this.form.date1 = "";
      this.form.date2 = "";
      this.form.delivery = false;
      this.form.type = [];
      this.form.resource = "";
      this.form.desc = "";
    },
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(() => {
          done();
        })
        .catch(() => {});
    },
    formatDate() {
      // 获取选定的日期
      const selectedDate = new Date(this.form.date1);

      // 将日期格式化为 YYYY-MM-DD
      const formattedDate = selectedDate.toISOString().split("T")[0];

      // 更新 form 中的日期属性
      this.form.date1 = formattedDate;
    },
    formatDateTime() {
      // 获取选定的日期时间
      const selectedDateTime = new Date(this.form.date2);

      // 获取小时、分钟和秒
      const hours = selectedDateTime
        .getHours()
        .toString()
        .padStart(2, "0");
      const minutes = selectedDateTime
        .getMinutes()
        .toString()
        .padStart(2, "0");
      const seconds = selectedDateTime
        .getSeconds()
        .toString()
        .padStart(2, "0");

      // 将时间格式化为 HH:mm:ss
      const formattedTime = `${hours}:${minutes}:${seconds}`;

      // 更新 form 中的日期时间属性
      this.form.date2 = formattedTime;
    },
    qxbtn() {
      this.form.name = "";
      this.form.region = "";
      this.form.date1 = "";
      this.form.date2 = "";
      this.form.delivery = false;
      this.form.type = [];
      this.form.resource = "";
      this.form.desc = "";
    }
  },
  mounted() {}
};
</script>


<style>
p {
  height: 50px;
  text-align: left;
  width: 100%;
}
</style>